.footer__copyright {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
}

@media screen and (max-width: 1100px) {
   .footer__copyright {
      font-size: 16px;
      line-height: 18px;
   }
}

@media screen and (max-width: 767px) {
   .footer__copyright  {
      font-size: 14px;
      line-height: 17px;
   }
 }
 