.photogallery {
  display: flex;
  max-width: 880px;
  margin: 45px auto 0;
  padding: 0;
}

@media screen and (max-width: 1100px) {
  .photogallery {
    max-width: 700px;
  }
	
}

@media screen and (max-width: 767px) {
  .photogallery {
    max-width: 100%;
    margin: 40px auto 0;
  }
}


